<template>
  <div id="side-nav">
    <div class="side-nav-element">
      <div class="side-nav-el-decoration" id="section1"></div>
      <a class="side-nav-element-link" href="javascript:void(0);" @click="sideNavClick($event)"><h3 id="1">HOME</h3></a>
    </div>
    <div class="side-nav-element">
      <div class="side-nav-el-decoration" id="section2"></div>
      <a class="side-nav-element-link" href="javascript:void(0);" @click="sideNavClick($event)"><h3 id="2">WHO AM I</h3></a>
    </div>
    <div class="side-nav-element">
      <div class="side-nav-el-decoration" id="section3"></div>
      <a class="side-nav-element-link" href="javascript:void(0);" @click="sideNavClick($event)"><h3 id="3">PORTFOLIO</h3></a>
    </div>
    <div class="side-nav-element">
      <div class="side-nav-el-decoration" id="section4"></div>
      <a class="side-nav-element-link" href="javascript:void(0);" @click="sideNavClick($event)"><h3 id="4">CONTACT ME</h3></a>
    </div>
  </div>
</template>

<script>
export default {
  name: "SideNavigation",
  beforeMount() {
    window.scrollTo(0, 0);
  },
  created() {
    window.addEventListener("scroll", () => {
      let winHeight = window.innerHeight;
      let curPos = window.pageYOffset;

      document.getElementById("section1").style.display =
          (curPos < 0.5*winHeight) ? "inline-block" : "none";
      document.getElementById("section2").style.display =
          (curPos >= 0.5*winHeight && curPos < 1.5*winHeight) ? "inline-block" : "none";
      document.getElementById("section3").style.display =
          (curPos >= 1.5*winHeight && curPos < 2.5*winHeight) ? "inline-block" : "none";
      document.getElementById("section4").style.display =
          (curPos >= 2.5*winHeight) ? "inline-block" : "none";
    });
  },
  methods: {
    sideNavClick: (event) => {
      switch (event.target.id) {
        case "1":
          document.getElementById("Home").scrollIntoView({behavior: "smooth"});
        break;
        case "2":
          document.getElementById("About").scrollIntoView({behavior: "smooth"});
        break;
        case "3":
          document.getElementById("Portfolio").scrollIntoView({behavior: "smooth"});
        break;
        case "4":
          document.getElementById("Contact").scrollIntoView({behavior: "smooth"});
        break;
      }
      document.getElementById("scroll-icon").style.display = "none";
    }
  }
}
</script>

<style scoped>
#side-nav {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-between;
  max-width: 35%;
  min-height: 50vh;
  position: fixed;
  top: 20%;
  left: 5%;
  text-align: left;
  z-index: 20;
}
.side-nav-element {
  white-space: nowrap;
  overflow: hidden;
}
.side-nav-el-decoration {
  width: 7.5vmin;
  height: 0.5vmin;
  background: white;
  margin-top: 1em;
  display: none;
  -webkit-animation: slide-right 0.5s ease-out;
  -moz-animation: slide-right 0.5s ease-out;
}
.side-nav-element-link {
  outline: 0;
  color: white;
  font-family: 'Iceberg', cursive;
  text-decoration: none;
  display: inline-block;
  margin-left: 1em;
  -webkit-animation: slide-right 2s ease-out;
  -moz-animation: slide-right 2s ease-out;
}
#section1 {
  display: inline-block;
}
@-webkit-keyframes slide-right {
  0% { opacity: 0; -webkit-transform: translateX(-100%); }
  100% { opacity: 1; -webkit-transform: translateX(0); }
}
@-moz-keyframes slide-right {
  0% { opacity: 0; -moz-transform: translateX(-100%); }
  100% { opacity: 1; -moz-transform: translateX(0); }
}
</style>